import React from "react";

// import EditImage from "./../../assets/pinceau.svg";

import Checkbox from "@material-ui/core/Checkbox";

import EditIcon from "@material-ui/icons/Edit";
import ClearIcon from "@material-ui/icons/Clear";
import "./highlight.style.scss";

const HighLight = ({
  item: { id, type, feedback, email, username, name, checked },
  remove,
  users_data,
  update,
  setIsWrt,
  setItems,
  flag,
  updateCurrentUserHighlights,
}) => {
  function fun() {
    console.log("id to edit:", id);
    setIsWrt(id);
  }

  function handleChange(event) {
    let newHghlt = {
      id: id,
      type,
      feedback,
      name,
      checked: event.target.checked,
    };

    let removedElement = users_data.filter((item) => item.id !== id);
    update(newHghlt);
    let updatedHighlights = [...removedElement, newHghlt];
    setItems(updatedHighlights);
    updateCurrentUserHighlights(updatedHighlights);
  }

  return (
    <div className="highlight-item">
      <div key={id} className="item-details">
        <label className="type">{type || username}</label>

        <span className="feedback">{feedback}</span>
        <span className="email">{email}</span>
        <b>
          <span className="name">{name}</span>
        </b>
      </div>
      {!flag ? (
        <div className="icon">
          <div>
            <Checkbox
              color="secondary"
              checked={checked}
              onChange={handleChange}
              size="medium"
            />
          </div>
          <div
            className="remove"
            onClick={() => {
              remove(id);
            }}
            style={{ margin: "7px" }}
          >
            <ClearIcon />
          </div>
          <div className="write" onClick={fun} style={{ margin: "7px" }}>
            <EditIcon />
          </div>
        </div>
      ) : null}
    </div>
  );
};

export default HighLight;
